body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}.App{background-color:#282c34;color:#ccc;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.Board{border-color:#ccc;border-style:solid;border-width:4px 4px 0 0}.Board-row{display:flex;align-items:center;justify-content:center}.Board-cell{border-color:#ccc;border-style:solid;border-width:0 0 4px 4px;height:100px;width:100px;cursor:pointer}.Board-cell.played{cursor:default}.FeedbackBar{border:4px solid #ccc;margin-bottom:4px}.noselect,.Board-cell{-webkit-touch-callout:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}

